/* Base Reset */
* {
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  background: linear-gradient(135deg, #1c1f26, #2e3440);
  color: #fff;
  margin: 0;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.input {
  padding: 8px 12px;
  border-radius: 8px;
  border: none;
  background: #3b4252;
  color: #fff;
  font-size: 16px;
  margin-left: 20px;
  outline: none;
  width: 60px;
  text-align: center;
}

.button {
  background: #5e81ac;
  border: none;
  color: white;
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.15s ease;
  margin: 8px;
  font-weight: 600;
}

.button:hover {
  transform: scale(1.05);
}

.slide-puzzle-board {
  display: inline-block;
  margin-top: 20px;
  border: 3px solid #4c566a;
  border-radius: 10px;
  padding: 12px;
  background-color: #2e3440;
  position: relative;
  box-shadow: 0 4px 15px rgba(0,0,0,0.4);
}

.square {
  background: #4c566a;
  border: 1px solid #3b4252;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 60px;
  height: 60px;
  width: 60px;
  margin: 2px;
  text-align: center;
  color: #eceff4;
  border-radius: 6px;
  transition: background 0.2s, transform 0.1s;
}

.square:hover {
  background: #5e81ac;
  transform: scale(1.05);
}

/* Status and Counter */
.status, .count {
  font-size: 18px;
  font-weight: 500;
  margin-left: 20px;
  margin-bottom: 10px;
}

.board-row:after {
  content: '';
  display: table;
  clear: both;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #2e3440;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: "fadeIn" 0.5s ease;
  z-index: "1000";
}

.modal-content {
  background: #9aa7c3;
  border-radius: 12px;
  padding: 40px 60px;
  text-align: center;
  color: #eceff4;
  box-shadow: 0 4px 20px rgba(0,0,0,0.5);
  position: relative;
}

.modal-content h2 {
  font-size: 2rem;
  margin-bottom: 10px;
  color: #2e3440;;
}

.modal-content p {
  font-size: 1.1rem;
  margin: 6px 0;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
